import { animated, config, useSpring } from "@react-spring/three";
import { Text3D } from "@react-three/drei";
import React, { Suspense, useEffect } from "react";
import pointerEvents from "./utils/pointerEvents";
import { FONTS } from "./assets";
import { useLoader } from "@react-three/fiber";
import { NearestFilter, TextureLoader } from "three";
import gradientTexture from "./assets/images/5-tone.jpg";

export default function Name() {
  const gradient5ToneMap = useLoader(TextureLoader, gradientTexture);
  gradient5ToneMap.minFilter = gradient5ToneMap.magFilter = NearestFilter;
  gradient5ToneMap.generateMipmaps = false;
  const lightSwitchSound = new Audio("sfx/light-switch.m4a");
  const [isFirstNameOn, setIsFirstNameOn] = React.useState(false);
  const [isLastNameOn, setIsLastNameOn] = React.useState(false);

  const [spring, api] = useSpring(() => ({
    firstNameColor: "#c0c0c0",
    lastNameColor: "#c0c0c0",
    config: config.stiff,
  }));

  useEffect(() => {
    setTimeout(() => {
      const firstNameCheckInterval = setInterval(() => {
        api.start({
          firstNameColor: "#fafafa",
        });
        api.start({
          lastNameColor: "#c0c0c0",
        });
      }, 500);

      const lastNameCheckInterval = setInterval(() => {
        api.start({
          firstNameColor: "#c0c0c0",
        });
        api.start({
          lastNameColor: "#fafafa",
        });
      }, 1000);

      setTimeout(() => {
        api.start({
          lastNameColor: "#c0c0c0",
        });
        clearInterval(firstNameCheckInterval);
        clearInterval(lastNameCheckInterval);
      }, 2400);
    }, 3 * 1000);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Suspense>
      <Text3D
        font={FONTS.SILKSCREEN_JSON}
        size={0.4}
        height={0.1}
        position={[-1.7, -0.9, 0.8]}
        rotation-x={-0}
        rotation-y={1.6}
        curveSegments={12}
        bevelEnabled
        bevelSize={0.01}
        bevelThickness={0.01}
        letterSpacing={-0.05}
        onClick={(e) => {
          e.stopPropagation();
          lightSwitchSound.play();
          api.start({
            firstNameColor: isFirstNameOn ? "#fafafa" : "#c0c0c0",
          });
          setIsFirstNameOn(!isFirstNameOn);
        }}
        castShadow
        {...pointerEvents}
      >
        Rizki
        <animated.meshToonMaterial
          color={spring.firstNameColor}
          gradientMap={gradient5ToneMap}
        />
      </Text3D>
      {/* <rectAreaLight
        width={2}
        height={1.5}
        intensity={30}
        color={"#BECFA5"}
        rotation={[0, -Math.PI / 2, 0]}
        position={[-1.8, -1.4, 0]}
      /> */}

      <Text3D
        font={FONTS.SILKSCREEN_JSON}
        size={0.32}
        height={0.07}
        position={[-1.45, -1.04, 1.5]}
        rotation-x={-Math.PI / 2}
        rotation-y={0}
        curveSegments={12}
        bevelEnabled
        bevelSize={0.01}
        bevelThickness={0.01}
        letterSpacing={-0.05}
        onClick={(e) => {
          e.stopPropagation();
          lightSwitchSound.play();
          api.start({
            lastNameColor: isLastNameOn ? "#fafafa" : "#c0c0c0",
          });
          setIsLastNameOn(!isLastNameOn);
        }}
        {...pointerEvents}
      >
        Fikriansyah
        <animated.meshToonMaterial
          color={spring.lastNameColor}
          gradientMap={gradient5ToneMap}
        />
      </Text3D>
    </Suspense>
  );
}
