import {
  Bounds,
  ContactShadows,
  Environment,
  SpotLight,
} from "@react-three/drei";
import FocusDeviceProvider from "./providers/FocusDeviceProvider";
import Interactables from "./Interactables/Interactables";
import { Perf } from "r3f-perf";
import { isProduction } from "./utils/isProduction";
import Particles from "./Particles/Particles";
import { isMobile } from "react-device-detect";

export default function Experience() {
  const isDevelopment = !isProduction();

  return (
    <>
      {isDevelopment && (
        <Perf openByDefault trackGPU={true} position="top-right" />
      )}
      <Environment preset="apartment" environmentIntensity={2} />
      <color args={["#1B2128"]} attach="background" />
      <SpotLight
        position={[-4, 5, 0]}
        angle={0.25}
        distance={7}
        attenuation={7}
        radiusTop={0.8}
        radiusBottom={2}
        color={"#FFF5B6"}
        penumbra={0.5}
        opacity={0.3}
        anglePower={5}
        intensity={10}
      />
      {!isMobile && (
        <directionalLight
          color="#fafada"
          position={[-5.6, 1.7, 5]}
          intensity={0.7}
        />
      )}

      <ambientLight intensity={0.5} color={"#fafada"} />

      {!isMobile && <Particles />}

      <FocusDeviceProvider>
        <Bounds fit clip observe margin={1.2} maxDuration={1}>
          <Interactables />
        </Bounds>
      </FocusDeviceProvider>

      <ContactShadows
        position-y={-1}
        opacity={0.3}
        scale={20}
        blur={5}
        far={10}
        frames={1}
        smooth
      />
    </>
  );
}
