import { useState } from "react";
import "./Welcome.scss";

const Welcome = () => {
  const isShowingLocalStorage =
    localStorage.getItem("isShowingWelcomeScreen") ?? "true";
  const [isShowingWelcomeScreen, setIsShowingWelcomeScreen] = useState(
    isShowingLocalStorage === "true"
  );

  const acceptWelcome = () => {
    setIsShowingWelcomeScreen(false);
    localStorage.setItem("isShowingWelcomeScreen", "false");
  };

  return (
    <>
      {isShowingWelcomeScreen && (
        <>
          <div className="welcome">
            <h1>A quick note before we begin...</h1>
            <p>This experience is intended for high-performance devices.</p>
            <p>
              If you are on low-performance devices, some features may not work
              as expected. I'm working on that and a mobile version as well, so
              stay tuned!
            </p>

            <p>
              If you are on a computer, please use the latest version of Google
              Chrome for the best experience.
            </p>

            <div className="welcome__cta">
              <button className="welcome__cta__mobile" disabled>
                Switch to Mobile Version
              </button>
              <button className="welcome__cta__start" onClick={acceptWelcome}>
                Show Me!
              </button>
            </div>
          </div>
          <div className="welcome-mask"></div>
        </>
      )}
    </>
  );
};

export default Welcome;
